.share{
    width: 97%;
    height: 170px;
    border-radius: 10px;
    margin-left: 10px;
    -webkit-box-shadow: 0px 0px 14px -4px rgba(0,0,0,0.75);
    -moz-box-shadow: 0px 0px 14px -4px rgba(0,0,0,0.75);
    box-shadow: 0px 0px 14px -4px rgba(0,0,0,0.75);
}
.shareWrapper{
    padding: 10px;
}

.shareTop{
    display: flex;
    align-items: center;
}
.shareProfileImg{
    width: 50px;
    height: 50px;
    border-radius: 50%;
    object-fit: cover;
    margin-right: 10px;
}

.shareInput{
    border: none;
    width: 80%;
}
.shareInput:focus{
    outline: none;
}

.shareHr{
    margin: 20px;
}
.shareBottom{
    display: flex;
    align-items: center;
    justify-content: space-between;
}
.shareOptions{
    display: flex;
    margin-left: 20px;
}
.shareOption{
    display: flex;
    align-items: center;
    margin-right: 15px;
    cursor: pointer;
}
.shareIcon{
    font-size: 18px;
    margin-right: 5px;
}
.bi-film{ color: tomato;}
.bi-tag{ color: blue;}
.bi-geo-alt{ color: green;}
.bi-emoji-laughing{ color: goldenrod;}

.shareOptionTex{
    font-size: 14px;
    font-weight: 500;
}
.shareButton{
    border: none;
    padding: 4px;
    border-radius: 5px;
    background-color: green;
    font-weight: 500;
    margin-right: 20px;
    cursor: pointer;
    color: white;
}